@font-face {
  font-family: FiraCode;
  src: url("/fonts/FiraCode-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  background-color: #fafafa;
  font-family: "Open Sans", sans-serif;
  line-height: 2em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212121;
  font-family: "Abel", sans-serif;
}

code {
  background-color: #000;
  border: 1px solid #404040;
  border-radius: 4px;
  color: #fff;
  font-family: "FiraCode";
  font-weight: lighter;
  padding: 10px 20px;
  margin: 10px 0 0;
}

code::before {
  color: #00cc00;
  content: '$ ';
}

li,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.site-container {
  margin: auto;
  max-width: 1170px;
  width: 100%;
}

.homework {
  display: grid;
}

.homework p {
  margin-top: 0;
}

@media only screen and (min-width: 768px) {
  .homework {
    grid-template-columns: 1fr 1fr;
  }
}

.gray-container {
  background-color: #f2f2f2;
  border: 1px solid #cccccc;
  border-radius: 4px;
  padding: 15px 4%;
  margin-top: 20px;
  width: 100%;
}

.large-container {
  padding-top: 50px;
  padding-bottom: 50px;
}
